.container {
    width: 750px;
    height: 450px;
    border-radius: 5px;
    border: 2px solid rgb(122, 6, 51);
    transition: all ease 0.3s;
    padding: 10px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 1px 1px 13px 0px rgba(227, 9, 9, 1);
}

#textarea {
    width: 80%;
    min-height: 200px;
    border-radius: 5px;
    border: 1px solid #fff;
    transition: all ease 0.3s;
    padding: 10px;
    background: #fff;
    font-size: 30px;
}

#count {
    font-size: 35px;
    color: #fff;
    padding: 10px;
}

@media screen and (max-width: 750px) {
    .container {
        flex-direction: column-reverse;
        width: 90vw;
        height: 300px;
    }
    #textarea {
        width: 90%;
        min-height: 150px;
    }
}